import { IKernelSpecs } from '@jupyterlite/kernel';
import { JavaScriptKernel } from '@jupyterlite/javascript-kernel';
/**
 * A plugin to register the JavaScript kernel.
 */
const kernel = {
    id: '@jupyterlite/javascript-kernel-extension:kernel',
    autoStart: true,
    requires: [IKernelSpecs],
    activate: (app, kernelspecs) => {
        kernelspecs.register({
            spec: {
                name: 'javascript',
                display_name: 'JavaScript',
                language: 'javascript',
                argv: [],
                spec: {
                    argv: [],
                    env: {},
                    display_name: 'JavaScript',
                    language: 'javascript',
                    interrupt_mode: 'message',
                    metadata: {}
                },
                resources: {
                    'logo-32x32': 'TODO',
                    'logo-64x64': '/kernelspecs/javascript.svg'
                }
            },
            create: async (options) => {
                return new JavaScriptKernel(options);
            }
        });
    }
};
const plugins = [kernel];
export default plugins;
